.product{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px; 
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: #fff;
    z-index: 1;
}

.product_rating{
    display: flex;
}

.product >img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;   
    margin-bottom: 15px;   
}

.product >button{
    background-color: #FFB3B3;
    margin-top: 10px;
    padding: 10px 20px;
    border-color: radial-gradient(circle, rgba(148,187,233,1) 10%, rgba(238,174,202,1) 54%);
    color: #111;
    font-weight: 400;
    border-radius: 20px;
    border-color: #FFB3B3;
    border: 3px solid #FF5D5D;

}

.product >button:hover{
    background-color: #FF5D5D;
}

.product_price{
    margin-top: 5px;
}
.product_detail{
    height: 100px;
    margin-bottom: 15px;
}

